@mixin vertically-centered {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

@mixin horizontally-centered {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

@mixin horiz-vert-centered {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  top: 25%;
}

@mixin horiz-vert-centered-mobile {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  top: 25%;

  @media screen and (min-width: map-get($breakpoints, medium)) {
    position: relative;
    left: auto;
    transform: none;
    top: auto;
  }
}
